import React from 'react';
import './header.css'
import {Link} from 'react-router-dom'
import {auth} from "../store/action";
import {connect} from 'react-redux';
import {AiOutlineSearch } from 'react-icons/ai'


class Appname extends React.Component{
    render(){
     //   console.log(this.props)
        return(
            <div className="topper">
            <img  src="data:image/png;base64," className="applogo" />   
             <input type="text"  className="location"  value="Pakistan" />   
             <input type="text" placeholder="Find Cars, Mobile Phones And More..." className="itemsearch" /> <button className="searchbutton" ><AiOutlineSearch /></button>
<button onClick={() => this.props.auth()} className="login">Login</button>
             <button className="loginbutton" > +Sell</button>
               
            </div>
        )
    }
}



const mapStateToProps  = (state) => ({
    user : state.user
})

const mapDispatchToProps = (dispatch) => ({
 auth : () => dispatch(auth())
} 
)


   export default 
       connect(mapStateToProps,mapDispatchToProps)(Appname);
   
   


